import React from "react";
import Illustration from '../Img/Illustration.png'
import './StartPage.css'

function StartPage (){
    return(
        <div className={'startPage__wrapper'}>
            <div className={'startPage__text'}>
                <h1 className={'startPage__title'}>  !</h1>
                <p className={'startPage__subtitle'}> ,      .   -  !</p>
                <form className={'startPage__form'} onSubmit={(event)=>event.preventDefault()}>
                    <label className={'startPage__label'}>
                        <input type={'text'} className={'startPage__input'} placeholder={''}/>
                        <button className={'startPage__button button'}></button>
                    </label>
                </form>
            </div>
            <div className={'startPage__visual'}>
                <img className={'startPage__pic'} src={Illustration} alt={'men'}/>
            </div>

        </div>
    )
}
export default StartPage;
